.page-footer {
  background-color: @white;
}

.page-footer__wrapper {
  .wrapper-mobile();

  @media (min-width: @tablet-width) {
    .wrapper-tablet();
  }

  @media (min-width: @desktop-width) {
    .wrapper-desktop();
  }
}

.page-footer__copyright {
  font-size: 16px;
  line-height: 100%;

  color: @special-grey;
}
