}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.page-title {
  font-weight: bold;
  font-size: 36px;
  line-height: 100%;
  color: #FFFFFF;
}
.button {
  display: block;
  font-family: "Blogger Sans", "Tahoma", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 100%;
  vertical-align: middle;
  color: #1D2E5B;
  border: none;
  background-color: #FFD74B;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .button {
    padding: 29px 38px 27px 33px;
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
  }
}
.page__body {
  position: relative;
  min-width: 320px;
  font-family: "Blogger Sans", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 200%;
  font-weight: 300;
  color: #444444;
}
.page__body--inner {
  background-color: #E5E5E5;
}
.main-nav__toggle {
  display: block;
  position: relative;
  width: 25px;
  height: 15px;
  background-color: transparent;
  border: none;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #FFFFFF;
  border-radius: 1.5px;
}
.main-nav__toggle::before {
  top: 0;
}
.main-nav__toggle::after {
  bottom: 0;
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__contacts,
  .main-nav__social {
    display: none;
  }
}
.main-nav__authorization {
  padding: 12px;
  width: auto;
  font-size: 17px;
  line-height: 176%;
  text-align: center;
  text-transform: uppercase;
  color: #161C35;
  border-radius: 25px;
}
@media (min-width: 1440px) {
  .main-nav__authorization {
    display: none;
  }
}
.site-list {
  margin-bottom: 51px;
  font-weight: 500;
  font-size: 30px;
  line-height: 117%;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .site-list {
    margin-bottom: 144px;
    font-size: 60px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .site-list {
    display: flex;
    justify-content: space-between;
    width: 628px;
    margin-bottom: 0;
    font-size: 20px;
    line-height: 20px;
  }
}
.site-list__link {
  color: #192144;
}
@media (min-width: 1440px) {
  .site-list__link {
    color: #FFFFFF;
  }
}
.user-list {
  display: flex;
}
.user-list__authorization {
  padding: 12px;
  width: auto;
  font-size: 17px;
  line-height: 176%;
  text-align: center;
  text-transform: uppercase;
  color: #161C35;
  background-color: #FFD74B;
  border-radius: 25px;
}
.user-list__contact {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.user-list__contact--call {
  background-color: #FF8D30;
}
.user-list__contact--email {
  background-color: #FFBD30;
}
.contacts-list__item {
  height: 45px;
}
.contacts-list__link {
  position: relative;
  padding: 14px 0 0 61px;
  font-weight: bold;
  font-size: 16px;
  line-height: 112%;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  color: #192144;
}
.contacts-list__link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50%;
}
.contacts-list__info--color-yellow {
  color: #FFD74B;
}
.promo__wrapper {
  position: relative;
  width: 270px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .promo__wrapper {
    width: 688px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .promo__wrapper {
    width: 1320px;
    margin: 0 auto;
  }
}
.promo {
  background-color: #161C35;
}
.promo__slogan,
.promo__quote {
  color: #FFFFFF;
}
.promo__slogan {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 111%;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__slogan {
    font-size: 60px;
  }
}
.promo__quote {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .quote__text {
    font-size: 28px;
  }
}
@media (min-width: 768px) {
  .quote__author {
    font-size: 21px;
  }
}
@media (min-width: 768px) {
  .promo__user {
    display: none;
  }
}
@media (min-width: 1440px) {
  .promo__user {
    display: flex;
    position: absolute;
    top: -40px;
    right: 0;
  }
}
.quote {
  font-size: 16px;
  line-height: 137%;
}
@media (min-width: 768px) {
  .quote {
    font-size: 28px;
    line-height: 33px;
  }
}
.quote__text {
  margin-bottom: 18px;
}
.quote__author {
  font-style: normal;
  font-weight: bold;
}
@media (min-width: 768px) {
  .quote__author {
    font-size: 21px;
    line-height: 25px;
  }
}
.instruction {
  display: flex;
  flex-direction: column;
  list-style: none;
}
@media (min-width: 768px) {
  .instruction {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .instruction {
    display: flex;
    flex-direction: row;
  }
}
.instruction__item {
  display: flex;
  justify-content: space-between;
  margin-top: -50px;
  padding: 22px 21px 75px 24px;
  font-weight: bold;
  font-size: 18px;
  line-height: 100%;
  background-color: #FF8D30;
  border-radius: 15px;
}
@media (min-width: 768px) {
  .instruction__item {
    flex-direction: column;
    margin-top: 0;
    padding: 31px 100px 21px 33px;
  }
}
@media (min-width: 1440px) {
  .instruction__item {
    flex-direction: row;
    justify-content: space-between;
    width: 1320px;
    padding: 30px 50px 30px 34px;
  }
}
.instruction__item:nth-child(2) {
  background-color: #FFA530;
}
.instruction__item:nth-child(3) {
  background-color: #FFBD30;
}
.instruction__item:last-child {
  background-color: #FFD74B;
}
@media (max-width: 767px) {
  .instruction__item:last-child {
    padding-bottom: 24px;
  }
}
@media (min-width: 768px) {
  .instruction__item:last-child {
    padding-bottom: 21px;
  }
}
@media (max-width: 767px) {
  .instruction__item:first-child {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .instruction__item:first-child,
  .instruction__item:nth-child(2) {
    border-radius: 15px 15px 0 0;
  }
}
@media (min-width: 1440px) {
  .instruction__item:first-child,
  .instruction__item:nth-child(2) {
    border-radius: 25px;
  }
}
@media (min-width: 768px) {
  .instruction__item:last-child,
  .instruction__item:nth-child(3) {
    border-radius: 0 0 15px 15px;
  }
}
@media (min-width: 1440px) {
  .instruction__item:last-child,
  .instruction__item:nth-child(3) {
    border-radius: 25px;
  }
}
.instruction__desc {
  width: 140px;
  color: #161C35;
}
@media (min-width: 768px) {
  .instruction__desc {
    width: 185px;
    font-size: 24px;
    line-height: 24px;
  }
}
.instruction__step {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .instruction__step {
    font-size: 30px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .instruction__step {
    font-size: 24px;
    line-height: 24px;
  }
}
